.header {
  background: #fff;
  padding: 16px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 80px;
  }
  &__right {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 50px;
  }
  &__logo {
    width: 389px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__list {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 15px;
    li a {
      font-size: 13px;
      line-height: 120%;
      font-weight: 500;
      text-transform: uppercase;
      letter-spacing: 0.65px;
    }
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 20px;
  }
  .header__btn {
    height: 46px;
    background: #3AAEF7;
    padding: 5px 25px;
    span {
      font-size: 13px;
      letter-spacing: 0.65px;
    }
  }
  &__social {
    width: 46px;
    &:hover {
      opacity: 0.7;
    }
    img {
      width: 100%;
    }
  }
  .whatsapp-fixed {
    @media screen and (min-width: 993px) {
      position: fixed;
      z-index: 5;
      top: 16px;
      right: max(30px, calc((100% - 1600px) / 2));
    }
  }
  &-mob {
    display: none;
  }
  .menu-bg {
    display: none;
  }
}
.burger {
  display: none;
}

@media screen and (max-width: 1500px) {
  .header {
    &__inner {
      gap: 50px;
    }
    &__right {
      gap: 20px;
    }
    &__logo {
      width: 350px;
    }
  }
}

@media screen and (max-width: 1400px) {
  .header {
    &__group {
      gap: 15px;
    }
    &__list {
      li a {
        font-size: 12px;
      }
    }
    &__btn {
      height: 39px;
      span {
        font-size: 12px;
      }
    }
    &__social {
      width: 39px;
    }
  }
}

@media screen and (max-width: 1350px) {
  .header {
    &__logo {
      width: 300px;
    }
  }
}

@media screen and (max-width: 1300px) {
  .header {
    &__inner {
      gap: 20px;
    }
  }
}

@media screen and (max-width: 1250px) {
  .header {
    &__left {
      display: flex;
      align-items: center;
      gap: 15px;
    }
    &__right {
      flex-grow: unset;
    }
    &__logo {
      width: 345px;
    }
    &__list {
      display: none;
    }
    &-mob {
      position: fixed;
      z-index: 10;
      top: 0;
      left: -105%;
      width: 400px;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      gap: 35px;
      background: #fff;
      padding: 25px 20px;
      transition: all 0.3s;
      overflow-y: auto;
      &--active {
        left: 0;
      }
      &__close {
        width: 40px;
        height: 40px;
        background: #F7F7F7;
        padding: 5px;
        img {
          max-width: 70%;
        }
      }
      .header__list {
        display: flex;
        flex-direction: column;
        align-items: start;
        gap: 30px;
        margin-top: 35px;
        li a {
          font-size: 15px;
          letter-spacing: 0.75px;
        }
      }
      .header__group {
        gap: 20px;
      }
      .header__btn {
        height: 46px;
        padding: 5px 25px;
        span {
          font-size: 13px;
        }
      }
      .header__social {
        width: 46px;
      }
    }
    .menu-bg {
      position: fixed;
      z-index: 5;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: block;
      background: rgba(33, 76, 215, 0.5);
      opacity: 0;
      pointer-events: none;
      transition: all 0.125s;
      &--active {
        opacity: 1;
        pointer-events: auto;
      }
    }
  }
  .burger {
    position: relative;
    width: 32px;
    height: 22px;
    display: block;
    cursor: pointer;
    span {
      position: absolute;
      left: 0;
      width: 100%;
      height: 2px;
      background: #214CD7;
      border-radius: 100px;
      transition: all 0.3s;
    }
    span:nth-child(1) {
      left: 0;
    }
    span:nth-child(2) {
      top: 50%;
      transform: translate(0, -50%);
    }
    span:nth-child(3) {
      bottom: 0;
    }
  }
}

@media screen and (max-width: 768px) {
  .header {
    &-mob {
      width: 300px;
    }
  }
}

@media screen and (max-width: 740px) {
  .header {
    &__logo {
      width: 250px;
    }
    &__btn {
      padding: 5px 20px;
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    &__logo {
      width: 300px;
    }
    &__group {
      display: none;
    }
    &-mob {
      width: 100%;
      .header__group {
        display: flex;
      }
    }
    .menu-bg {
      display: none;
    }
  }
}

@media screen and (max-width: 374px) {
  .header {
    &__logo {
      width: 245px;
    }
  }
}