.wrapper {
  overflow: hidden;
}
.btn-blue {
  background: #6BC6FF;
  border-radius: 100px;
  color: #fff;
  &:hover {
     background: #214CD7;
   }
  span {
    line-height: 120%;
    font-weight: 500;
    text-transform: uppercase;
  }
}
.btn-blue2 {
  background: #214CD7;
  border-radius: 100px;
  color: #fff;
  &:hover {
    background: #4d6ee2;
  }
  span {
    line-height: 120%;
    font-weight: 500;
    text-transform: uppercase;
  }
}
.h1,
.h2 {
  font-size: 40px;
  line-height: 120%;
  font-weight: 700;
  text-transform: uppercase;
}
.text {
  display: grid;
  grid-template-columns: 100%;
  gap: 20px;
  margin-top: 20px;
  p {
    font-size: 20px;
    line-height: 150%;
  }
}
.swiper-button-prev,
.swiper-button-next {
  width: 70px;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: transparent;
  border: 2px solid #6BC6FF;
  border-radius: 50%;
  &:after {
    display: none;
  }
  svg {
    width: 10px;
    height: auto;
  }
}
.swiper-pagination {
  &-bullet {
    width: 16px;
    height: 16px;
    background: #6BC6FF;
    margin: 0 16px !important;
    opacity: 1 !important;
  }
  &-bullet-active {
    background: #214CD7 !important;
  }
}
.modal {
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background: rgba(33, 76, 215, 0.5);
  padding: 0 15px;
  opacity: 0;
  pointer-events: none;
  overflow-y: auto;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    position: relative;
    max-width: 455px;
    width: calc(100% - 30px);
    background: #fff;
    border-radius: 20px;
    padding: 40px 60px;
    margin: 0 auto;
    @media screen and (max-width: 992px) {
      margin: 50px auto;
    }
  }
  &__close {
    position: absolute;
    z-index: 2;
    top: 15px;
    right: 15px;
    width: 20px;
    background: transparent;
    padding: 0;
    img {
      width: 100%;
      height: auto;
    }
  }
  &__title {
    font-size: 25px;
    line-height: 120%;
    font-weight: 700;
    text-transform: uppercase;
  }
  .form {
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
    margin-top: 20px;
    &-group {
      width: 100%;
      .input-title {
        display: block;
        font-size: 16px;
        line-height: 150%;
        margin-bottom: 5px;
        span {
          color: #F83062;
        }
      }
      input {
        width: 100%;
        height: 48px;
        background: #F3F3F3;
        border: 1px solid transparent;
        border-radius: 10px;
        padding: 0 20px;
        font-size: 18px;
        line-height: 120%;
        color: #47535B;
        &::placeholder {
          color: #47535B;
        }
      }
    }
    &-btn {
      height: 54px;
      padding: 5px 35px;
      margin-top: 20px;
      span {
        font-size: 17px;
      }
    }
    &-info {
      text-align: center;
      font-size: 16px;
      line-height: 150%;
      margin-top: 20px;
      a {
        color: #214CD7;
        &:hover {
          color: #6BC6FF;
        }
      }
    }
  }
  &__icon {
    width: 74px;
    img {
      width: 100%;
    }
  }
  &__text {
    max-width: 250px;
    p {
      font-size: 18px;
      line-height: 120%;
    }
  }
  &__btn {
    height: 54px;
    padding: 5px 35px;
    span {
      font-size: 17px;
    }
  }
}
.modal-accepted {
  .modal__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 30px;
  }
  .modal__title {
    text-transform: unset;
  }
}

@media screen and (max-width: 1300px) {
  .text {
    p {
      font-size: 18px;
    }
  }
}

@media screen and (max-width: 992px) {
  .h1,
  .h2 {
    font-size: 30px;
  }
  .text {
    p {
      font-size: 16px;
    }
  }
  .swiper-button-prev,
  .swiper-button-next {
    width: 54px;
    height: 54px;
    svg {
      width: 8px;
    }
  }
  .swiper-pagination {
    &-bullet {
      width: 10px;
      height: 10px;
      margin: 0 10px !important;
    }
  }
}

@media screen and (max-width: 576px) {
  .h1,
  .h2 {
    font-size: 20px;
  }
  .text {
    gap: 10px;
    p {
      font-size: 14px;
    }
  }
  .modal {
    &__dialog {
      padding: 40px 20px;
    }
    &__title {
      font-size: 20px;
    }
    .form {
      &-btn {
        margin-top: 10px;
      }
      &-info {
        font-size: 14px;
        margin-top: 10px;
      }
    }
    &__title {
      margin-top: 25px;
    }
    &__text {
      margin-top: 5px;
    }
    &__btn {
      margin-top: 25px;
    }
  }
  .modal-accepted {
    .modal__content {
      gap: 0;
    }
  }
}