.main {
  position: relative;
  color: #fff;
  &:before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: calc(100% - 170px);
    height: 100%;
    background-image: url("../img/main-img.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    pointer-events: none;
  }
  &__inner {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    padding-right: max(30px, calc((100% - 1600px) / 2));
  }
  &__desc {
    position: relative;
    width: calc(100% - 925px);
    border-radius: 0 100px 100px 0;
    padding: 100px 0 200px;
    overflow: hidden;
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: #6BC6FF;
      border-radius: 0 100px 100px 0;
      pointer-events: none;
    }
  }
  .empty-block {
    width: 925px;
    pointer-events: none;
  }
  &__group {
    position: relative;
    z-index: 3;
    max-width: 705px;
    padding: 0 125px 0 30px;
    margin-left: auto;
  }
  &__img {
    position: absolute;
    z-index: 2;
    right: 0;
    bottom: 0;
    width: 100%;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  &__text {
    font-size: 25px;
    line-height: 120%;
    font-weight: 300;
    margin-top: 40px;
    opacity: 95%;
  }
  &__btn {
    height: 63px;
    padding: 5px 35px;
    margin-top: 40px;
    span {
      font-size: 18px;
    }
  }
  &__img-mob {
    display: none;
  }
}
.services {
  position: relative;
  z-index: 2;
  padding-bottom: 60px;
  margin-top: -100px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 80px;
    background: #fff;
    box-shadow: 0px 30px 60px 0px rgb(230, 238, 245);
    border-radius: 80px;
    padding: 80px;
  }
  &__desc {
    width: 375px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 30px;
  }
  &__cards {
    width: calc(100% - 455px);
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 60px / 3));
    gap: 30px;
  }
  &__text {
    margin-top: 20px;
    opacity: 0.7;
  }
  &__info {
    font-size: 25px;
    line-height: 120%;
    font-weight: 600;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
    margin-top: 30px;
  }
  &__item {
    display: flex;
    align-items: center;
    gap: 15px;
    &-icon {
      width: 45px;
      height: 45px;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #6BC6FF;
      border-radius: 50%;
      padding: 5px;
      img {
        max-width: 70%;
      }
    }
    &-phone {
      font-size: 25px;
      line-height: 120%;
      font-weight: 600;
      color: #214CD7;
      &:hover {
        color: #6BC6FF;
      }
    }
    &-text {
      font-size: 16px;
      line-height: 150%;
      opacity: 0.7;
    }
  }
  &__mob {
    display: none;
  }
}
.service-card {
  display: flex;
  flex-direction: column;
  &__img {
    position: relative;
    padding-bottom: 75%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 20px 20px 0 0;
    }
  }
  &__desc {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    background: #F5F9FD;
    border-radius: 0 0 20px 20px;
    padding: 15px 30px 30px 30px;
  }
  &__title {
    font-size: 16px;
    line-height: 150%;
    font-weight: 600;
    text-transform: uppercase;
    color: #000;
  }
  &__text {
    font-size: 16px;
    line-height: 130%;
    font-weight: 300;
    margin-top: 5px;
    margin-bottom: auto;
  }
  &__btn {
    height: 50px;
    padding: 5px 15px;
    margin-top: 15px;
    span {
      font-size: 16px;
    }
  }
}
.about {
  padding: 60px 0 70px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 80px;
  }
  &__img {
    width: 480px;
    img {
      width: 100%;
      border-radius: 100px 10px 100px 10px;
    }
    img:nth-child(2) {
      display: none;
    }
  }
  &__desc {
    width: calc(100% - 560px);
    display: flex;
    flex-direction: column;
  }
  &__text {
    margin-top: 20px;
    margin-bottom: auto;
    opacity: 0.7;
  }
  &__items {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 30px;
    margin-top: 30px;
  }
  &__item {
    max-width: 295px;
    &-number {
      font-size: 60px;
      line-height: 100%;
      font-weight: 900;
      color: #214CD7;
      span {
        font-size: 40px;
        font-weight: 700;
      }
    }
    &-text {
      font-size: 22px;
      line-height: 140%;
      color: #214CD7;
      opacity: 0.7;
    }
  }
}
.calculator {
  background: #fff;
  padding: 100px 0;
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 40px;
    margin-top: 20px;
  }
  &__slider {
    position: relative;
    width: calc(100% - 410px);
    display: flex;
    flex-direction: column;
  }
  &__total {
    width: 370px;
    display: flex;
    flex-direction: column;
    color: #fff;
  }
  &__total-top {
    background: #1039BF;
    border-radius: 30px 30px 0 0;
    padding: 20px 40px;
  }
  &__total-bottom {
    position: relative;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    background: #214CD7;
    border-radius: 0 0 30px 30px;
    padding: 30px 40px 40px 40px;
  }
  &__total-img {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  &__price-text {
    font-size: 20px;
    line-height: 120%;
    font-weight: 500;
  }
  &__price {
    font-size: 45px;
    line-height: 120%;
    font-weight: 700;
    span {
      margin-left: 5px;
      opacity: 0.5;
    }
  }
  &__list {
    position: relative;
    z-index: 2;
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    margin-bottom: auto;
    li {
      display: flex;
      justify-content: space-between;
      align-items: start;
      span {
        font-size: 17px;
        line-height: 120%;
      }
      span:first-child {
        opacity: 0.7;
      }
      span:nth-child(2) {
        text-align: right;
      }
    }
  }
  &__btn {
    position: relative;
    z-index: 2;
    width: 100%;
    height: 64px;
    border-radius: 10px;
    padding: 5px 10px;
    margin-top: 95px;
    &:hover {
      background: rgba(107, 198, 255, 0.7);
    }
    span {
      font-size: 20px;
    }
  }
  .swiper {
    width: 100%;
    margin-bottom: auto;
  }
  &__subtitle {
    font-size: 25px;
    line-height: 150%;
    margin-bottom: 25px;
    opacity: 0.7;
  }
  &__slider-nav {
    display: flex;
    align-items: flex-end;
    gap: 20px;
    margin-top: 30px;
  }
  &__slider-pagination {
    flex-grow: 1;
  }
  .swiper-pagination {
    position: static;
    &-progressbar {
      height: 5px;
      flex-grow: 1;
      background: #E9F0F7;
      border-radius: 100px;
    }
    &-progressbar-fill {
      background: #214CD7;
    }
  }
  .step-pagination {
    font-size: 30px;
    line-height: 120%;
    font-weight: 500;
    margin-bottom: 15px;
    &__number {
      font-weight: 900;
      color: #6BC6FF;
      margin-left: 5px;
    }
  }
  &__slider-btns {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    gap: 20px;
  }
  .swiper-button-prev {
    position: static;
    width: 64px;
    height: 64px;
    background: #E9F0F7;
    border: none;
    margin-top: 0;
    &:hover {
      opacity: 0.7;
    }
  }
  .swiper-button-next {
    position: static;
    width: auto;
    height: 64px;
    background: #6BC6FF;
    border-radius: 100px;
    padding: 5px 50px;
    margin-top: 0;
    &:hover {
      background: #214CD7;
    }
    span {
      font-size: 20px;
    }
  }
  .swiper-button-disabled {
    opacity: 0.7;
  }
  &__checkboxes {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(6, calc(100% / 6 - 200px / 6));
    gap: 40px;
  }
  &__checkbox {
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ .checkbox-card {
      border-color: #6BC6FF;
      .checkbox-card--active {
        opacity: 1;
        pointer-events: auto;
      }
    }
  }
  .checkbox-card {
    position: relative;
    height: 100%;
    display: block;
    border: 2px solid #DDE3E9;
    border-radius: 20px;
    padding: 18px;
    &__img {
      position: relative;
      display: block;
      padding-bottom: 100%;
      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-radius: 5px;
      }
    }
    &__title {
      display: block;
      font-size: 16px;
      line-height: 120%;
      margin-top: 10px;
    }
    &--active {
      position: absolute;
      z-index: 2;
      top: 0;
      right: 0;
      width: 34px;
      height: 34px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #6BC6FF;
      border-radius: 50%;
      opacity: 0;
      pointer-events: none;
      img {
        max-width: 80%;
      }
    }
    &__desc {
      flex-grow: 1;
    }
    &__img2 {
      width: 115px;
      height: 115px;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #6BC6FF;
      border-radius: 5px;
      padding: 5px;
      img {
        max-width: 60%;
        max-height: 100%;
      }
    }
    &__select {
      height: 45px;
      display: block;
      margin-top: 10px;
    }
    .nice-select {
      width: 100%;
      height: 45px;
      display: flex;
      align-items: center;
      background: #F5F9FD !important;
      border: none;
      border-radius: 5px;
      padding: 0 25px 0 15px;
      white-space: unset;
      font-size: 16px;
      line-height: 120%;
      color: #214CD7;
      &:after {
        top: 50%;
        right: 15px;
        transform: translate(0, -50%);
        width: 14px;
        height: 9px;
        background-image: url("../img/arrow-bottom.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        transition: all 0.3s;
      }
      .list {
        li {
          padding: 10px;
          font-size: 16px;
          line-height: 120%;
        }
      }
    }
    .open {
      &:after {
        transform: translate(0, -50%) rotate(-180deg);
      }
    }
    &__price {
      display: block;
      font-size: 25px;
      line-height: 100%;
      font-weight: 700;
      color: #214CD7;
      margin-top: 10px;
      span {
        opacity: 0.7;
        margin-left: 5px;
      }
    }
    &__img3 {
      width: 100%;
      height: 115px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #6BC6FF;
      border-radius: 5px;
      padding: 5px;
    }
  }
  &__checkboxes2,
  &__checkboxes3 {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 80px / 3));
    gap: 40px;
  }
  .checkbox-card2 {
    display: flex;
    align-items: center;
    gap: 15px;
    .checkbox-card__title {
      margin-top: 0;
    }
  }
  .checkbox-card3 {
    .checkbox-card__desc {
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
      margin-top: 15px;
    }
  }
  &__checkboxes4 {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 40px / 2));
    gap: 40px;
  }
}
.feedback {
