.footer {
  &-top {
    background-image: url("../img/footer-img.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-color: #214CD7;
    padding: 80px 0;
  }
  &-bottom {
    background: #47535B;
    padding: 35px 0;
  }
  &-bottom__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__text {
    max-width: 550px;
    text-align: center;
    font-size: 17px;
    line-height: 150%;
    font-weight: 300;
    color: #fff;
    margin: 0 auto;
    a {
      display: block;
      color: #6BC6FF;
      &:hover {
        color: #214CD7;
      }
    }
  }
  .form {
    &-wrap {
      display: flex;
      justify-content: space-between;
      align-items: start;
      gap: 60px;
    }
    &-desc {
      width: 460px;
    }
    &-title {
      color: #fff;
    }
    &-subtitle {
      font-size: 30px;
      line-height: 120%;
      color: #fff;
      margin-top: 10px;
      opacity: 0.7;
    }
    &-grid {
      width: calc(100% - 520px);
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 40px / 2));
      gap: 40px;
    }
    &-group {
      position: relative;
      &--big {
        grid-column: 2/3;
        grid-row: 1/3;
      }
      input {
        width: 100%;
        height: 56px;
        background: #fff;
        border: 1px solid transparent;
        border-radius: 10px;
        padding: 0 30px 0 60px;
        font-size: 20px;
        line-height: 120%;
        color: #47535B;
        &::placeholder {
          color: rgba(71, 83, 91, 0.7) !important;
        }
      }
      textarea {
        width: 100%;
        height: 100%;
        background: #fff;
        border: 1px solid transparent;
        border-radius: 10px;
        padding: 15px 30px;
        resize: none;
        font-size: 20px;
        line-height: 120%;
        color: #47535B;
        &::placeholder {
          color: rgba(71, 83, 91, 0.7) !important;
        }
      }
      .input-icon {
        position: absolute;
        top: 50%;
        left: 25px;
        transform: translate(0, -50%);
        width: 16px;
        img {
          width: 100%;
        }
      }
    }
    &-wrap2 {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 20px;
      margin-top: 40px;
    }
    &-info {
      font-size: 20px;
      line-height: 120%;
      color: #fff;
      a {
        color: #6bc6ff;
      }
    }
    &-btn {
      height: 64px;
      padding: 5px 60px;
      span {
        font-size: 20px;
      }
    }
  }
  &__logo {
    max-width: 345px;
    display: block;
    margin-top: 15px;
    img {
      width: 100%;
    }
  }
}

@media screen and (max-width: 1500px) {
  .footer {
    .form {
      &-wrap {
        gap: 40px;
      }
      &-desc {
        width: 400px;
      }
      &-subtitle {
        font-size: 20px;
      }
      &-grid {
        width: calc(100% - 440px);
      }
    }
  }
}

@media screen and (max-width: 1300px) {
  .footer {
    .form {
      &-wrap {
        flex-direction: column;
        align-items: center;
        gap: 25px;
      }
      &-desc {
        width: 100%;
        text-align: center;
      }
      &-grid {
        width: 100%;
        grid-template-columns: repeat(2, calc(50% - 20px / 2));
        gap: 20px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .footer {
    &-top {
      padding: 50px 0;
    }
    &-bottom {
      padding: 25px 0;
    }
    &__text {
      font-size: 15px;
    }
    .form {
      &-subtitle {
        max-width: 460px;
        font-size: 16px;
        margin-left: auto;
        margin-right: auto;
      }
      &-group {
        input,
        textarea {
          font-size: 16px;
        }
      }
      &-info {
        max-width: 400px;
        font-size: 16px;
      }
      &-btn {
        height: 52px;
        padding: 5px 30px;
        span {
          font-size: 18px;
        }
      }
    }
  }
}

@media screen and (max-width: 740px) {
  .footer {
    .form {
      &-grid {
        grid-template-columns: 100%;
      }
      &-group {
        &--big {
          order: 2;
          grid-column: unset;
          grid-row: unset;
        }
        textarea {
          height: 133px;
        }
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .footer {
    &-top {
      padding: 40px 0;
    }
    &-bottom {
      padding: 20px 0;
    }
    &__text {
      max-width: 315px;
      font-size: 14px;
    }
    .form {
      &-grid {
        gap: 15px;
      }
      &-subtitle {
        max-width: 345px;
        font-size: 14px;
      }
      &-wrap2 {
        flex-direction: column;
        text-align: center;
        gap: 15px;
        margin-top: 15px;
      }
      &-btn {
        order: -1;
        span {
          font-size: 14px;
        }
      }
      &-info {
        font-size: 14px;
      }
    }
  }
}